<template>
  <el-form :rules="rules" class="login-container" label-position="left" label-width="0px" v-loading="loading" ref="loginForm">
    <h3 class="login_title"></h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder=""></el-input>
    </el-form-item>
    <el-checkbox class="login_remember" v-model="checked" label-position="left"></el-checkbox>
    <el-form-item style="width: 100%">
      <el-button type="primary" @click.native.prevent="submitClick" style="width: 100%"></el-button>
    </el-form-item>


    <!--  -->
    <el-form-item style="width: 100%; margin-top: 10px">
      <el-button
        type="success"
        @click.native.prevent="registerClick"
        style="width: 100%">
        
      </el-button>
    </el-form-item>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="registerDialogVisible"
      width="400px"
      :close-on-click-modal="false"
    >
      <el-form
        :model="registerForm"
        :rules="registerRules"
        ref="registerForm"
        label-width="80px"
      >
        <el-form-item label="" prop="username">
          <el-input
            v-model="registerForm.username"
            placeholder="4-20"
            prefix-icon="el-icon-user"
          ></el-input>
        </el-form-item>

        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="registerForm.password"
            show-password
            placeholder="8"
            prefix-icon="el-icon-lock"
          ></el-input>
        </el-form-item>

        <el-form-item label="" prop="checkPassword">
          <el-input
            type="password"
            v-model="registerForm.checkPassword"
            show-password
            placeholder=""
            prefix-icon="el-icon-refresh"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input
            v-model="registerForm.nickname"
            placeholder=""
            prefix-icon="el-icon-edit"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input
            v-model="registerForm.email"
            placeholder=""
            prefix-icon="el-icon-message"
          ></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="registerDialogVisible = false"></el-button>
        <el-button
          type="primary"
          @click="submitRegister"
          :loading="registerLoading"
        >
          
        </el-button>
      </div>
    </el-dialog>

  </el-form>
</template>
<script>
  import {postRequest} from '../utils/api'
  import {putRequest} from '../utils/api'
  export default{
    data(){
      return {
        rules: {
          // account: [{required: true, message: '', trigger: 'none'}],
          // checkPass: [{required: true, message: '', trigger: 'none'}]
        },
        checked: true,
        loginForm: {
          username: 'sang',
          password: '123'
        },
        loading: false,

        // 
        registerDialogVisible: false,
        registerLoading: false,

        // 
        registerForm: {
          username: '',
          password: '',
          checkPassword: '',
          nickname: '',
          email: '',
          agreement: false
        },

        // 
        registerRules: {
          username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 4, max: 20, message: ' 4  20 ', trigger: 'blur' },
            { pattern: /^[a-zA-Z0-9_]+$/, message: '', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
            { min: 8, message: '8', trigger: 'blur' }
          ],
          checkPassword: [
            { required: true, message: '', trigger: 'blur' },
            { validator: this.validatePassword, trigger: 'blur' }
          ]
        }

      }
    },
    methods: {
      submitClick: function () {
        var _this = this;
        this.loading = true;
        postRequest('/login', {
          username: this.loginForm.username,
          password: this.loginForm.password
        }).then(resp=> {
          _this.loading = false;
          if (resp.status == 200) {
            //
            var json = resp.data;
            if (json.status == 'success') {
              _this.$router.replace({path: '/home'});
            } else {
              _this.$alert('!', '!');
            }
          } else {
            //
            _this.$alert('!', '!');
          }
        }, resp=> {
          _this.loading = false;
          _this.$alert('!', '!');
        });
      },

      //  - 
      registerClick() {
        this.resetRegisterForm();
        this.registerDialogVisible = true;
      },

      // 
      validatePassword(rule, value, callback) {
        if (value !== this.registerForm.password) {
          callback(new Error(''));
        } else {
          callback();
        }
      },

      // 
      resetRegisterForm() {
        this.registerForm = {
          username: '1234',
          password: '12341234',
          checkPassword: '12341234',
          nickname: '',
          email: '',
          agreement: false
        };

        // 
        if (this.$refs.registerForm) {
          this.$refs.registerForm.clearValidate();
        }
      },

      // 
      submitRegister() {
        this.$refs.registerForm.validate(valid => {
          if (!valid) {
            this.$message.error('');
            return false;
          }

          this.registerLoading = true;

          // 
          const registerData = {
            username: this.registerForm.username,
            password: this.registerForm.password,
            nickname: this.registerForm.nickname || undefined,
            email: this.registerForm.email || undefined
          };

          // 
          postRequest('/reg', registerData)
            .then(response => {
              this.registerLoading = false;
              if (!(response && response.data)) {
                this.$message.error('');
                return;
              }
              const resData = response.data;
              // 
              if (typeof resData.code === 'undefined' || typeof resData.message === 'undefined') {
                this.$message.error('');
                return;
              }
              // 
              if (resData.code === 0) {
                this.$message.success('ID' + (resData.userId || ''));
                //this.$router.push('/login');
                this.registerDialogVisible = false;
              } else {
                this.$message.error('' + resData.message);
              }
            })
            .catch(error => {
              this.registerLoading = false;
              this.$message.error('' + error.message);
            });
        });
      }
    }
  }
</script>
<style>
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: left;
  }

  .dialog-footer {
    text-align: center;
  }

</style>



