<template>
  <el-card v-loading="loading" style="width: 700px; margin: 0 auto;">
    <div slot="header" class="card-header">
      <span style="font-size: 18px; font-weight: bold;"></span>
    </div>

    <div class="profile-container">
<!--      &lt;!&ndash;  &ndash;&gt;-->
<!--      <div class="avatar-section">-->
<!--        <el-avatar :size="120" :src="user.avatar" shape="circle"></el-avatar>-->
<!--        <div class="avatar-actions">-->
<!--          <input-->
<!--            type="file"-->
<!--            ref="avatarInput"-->
<!--            style="display: none"-->
<!--            @change="handleAvatarChange"-->
<!--            accept="image/*"-->
<!--          >-->
<!--          <el-button-->
<!--            type="primary"-->
<!--            size="small"-->
<!--            icon="el-icon-upload"-->
<!--            @click="$refs.avatarInput.click()"-->
<!--          >-->
<!--            -->
<!--          </el-button>-->
<!--          <p style="color: #909399; font-size: 12px; margin-top: 8px;">-->
<!--             JPG/PNG 5MB-->
<!--          </p>-->
<!--        </div>-->
<!--      </div>-->

      <!--  -->
      <div class="info-section">
        <el-form :model="user" label-width="90px" size="small">
          <!--  -->
          <div class="form-group">
            <div class="form-header">
              <i class="el-icon-user-solid"></i>
              <span class="section-title"></span>
            </div>

            <el-form-item label="">
              <el-input v-model="user.username" disabled></el-input>
            </el-form-item>

            <el-form-item label="ID">
              <el-input v-model="user.id" disabled></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="user.nickname" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="" prop="email">
              <el-input
                v-model="user.email"
                type="email"
                placeholder=""
                @blur="validateEmail"
              ></el-input>
              <div
                v-if="emailError"
                style="color: #f56c6c; font-size: 12px; margin-top: 5px;"
              >
                {{ emailError }}
              </div>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="user.regTime" disabled></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-tag :type="user.enabled ? 'success' : 'danger'" size="medium">
                {{ user.enabled ? '' : '' }}
              </el-tag>
            </el-form-item>

            <!--  -->
            <div style="text-align: center; margin-top: 15px;">
              <el-button
                type="primary"
                @click="saveBasicInfo"
                :loading="basicSaving"
                style="width: 60%;"
              >
                
              </el-button>
            </div>
          </div>

          <!--  -->
          <div class="form-group" style="margin-top: 30px; border-top: 1px solid #ebeef5; padding-top: 20px;">
            <div class="form-header">
              <i class="el-icon-lock"></i>
              <span class="section-title"></span>
            </div>

            <el-form-item label="">
              <el-input
                v-model="passwordForm.currentPassword"
                type="password"
                placeholder=""
                show-password
              ></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-input
                v-model="passwordForm.newPassword"
                type="password"
                placeholder=""
                show-password
                @input="validatePassword"
              ></el-input>
              <div
                v-if="passwordError"
                style="color: #f56c6c; font-size: 12px; margin-top: 5px;"
              >
                {{ passwordError }}
              </div>
            </el-form-item>

            <el-form-item label="">
              <el-input
                v-model="passwordForm.confirmPassword"
                type="password"
                placeholder=""
                show-password
                @blur="validatePasswordConfirmation"
              ></el-input>
              <div
                v-if="confirmPasswordError"
                style="color: #f56c6c; font-size: 12px; margin-top: 5px;"
              >
                {{ confirmPasswordError }}
              </div>
            </el-form-item>

            <!--  -->
            <div style="text-align: center; margin-top: 20px;">
              <el-button
                type="primary"
                @click="savePassword"
                :loading="passwordSaving"
                style="width: 60%;"
              >
                
              </el-button>
            </div>
          </div>
        </el-form>
      </div>
    </div>
  </el-card>
</template>

<script>
import {getRequest, putRequest, uploadAvatar} from '../utils/api'
import axios from "axios";

export default {
  data() {
    return {
      loading: false,
      basicSaving: false, // 
      passwordSaving: false, // 
      emailError: '',
      passwordError: '',
      confirmPasswordError: '',
      user: {
        id: '',
        username: '',
        nickname: '',
        email: '',
        avatar: '',
        regTime: '',
        enabled: true
      },
      passwordForm: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      }
    }
  },
  mounted() {
    this.loadUserInfo();
  },
  methods: {
    // 
    async loadUserInfo() {
      this.loading = true;
      try {
        const response = await getRequest("/userInfo");
        if (response.status === 200 && response.data) {
          const userData = response.data.data || response.data;
          this.user = {
            id: userData.id,
            username: userData.username,
            nickname: userData.nickname,
            email: userData.email,
            avatar: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
            regTime: userData.regTime ? new Date(userData.regTime).toLocaleString() : "",
            enabled: userData.enabled
          };
        }
      } catch (error) {
        this.$message.error('');
        console.error(":", error);
      } finally {
        this.loading = false;
      }
    },

    // 
    async handleAvatarChange(e) {
      const file = e.target.files[0];
      if (!file || !file.type.match('image.*')) {
        this.$message.error('');
        return;
      }

      if (file.size > 5 * 1024 * 1024) {
        this.$message.error('5MB');
        return;
      }

      // FormData
      const formData = new FormData();
      formData.append('avatar', file);

      try {
        this.loading = true;

        // 
        const uploadResponse = await axios.post("updateAvatar", formData);

        if (uploadResponse.status !== 200 || !uploadResponse.data.msg) {
          this.$message.error(':' + uploadResponse.status);
        }

        this.$message.success('');

        // 2.  URL 
        const response = await getRequest("/userInfo");
        if (response.status === 200 && response.data) {
          const userData = response.data.data || response.data;
          this.user.avatar = userData.avatar;
          this.$message.success('');
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error(":" + error.message);
      } finally {
        this.loading = false;
        e.target.value = '';
      }
    },

    // 
    validateEmail() {
      if (!this.user.email) {
        this.emailError = '';
        return true;
      }

      const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (regex.test(this.user.email)) {
        this.emailError = '';
        return true;
      } else {
        this.emailError = '';
        return false;
      }
    },

    // 
    validatePassword() {
      if (!this.passwordForm.newPassword) {
        this.passwordError = '';
        return true;
      }

      if (this.passwordForm.newPassword.length < 8) {
        this.passwordError = '8';
        return false;
      } else {
        this.passwordError = '';
        return true;
      }
    },

    // 
    validatePasswordConfirmation() {
      if (!this.passwordForm.confirmPassword) {
        this.confirmPasswordError = '';
        return true;
      }

      if (this.passwordForm.newPassword !== this.passwordForm.confirmPassword) {
        this.confirmPasswordError = '';
        return false;
      } else {
        this.confirmPasswordError = '';
        return true;
      }
    },

    // 
    async saveBasicInfo() {
      // 
      if (!this.validateEmail()) {
        this.$message.warning('');
        return;
      }

      this.basicSaving = true;

      try {
        // 
        const updateData = {
          nickname: this.user.nickname,
          email: this.user.email
        };

        // 
        const response = await putRequest("/updateUserInfo", updateData);

        if (response.status === 200) {
          this.$message.success('');
        } else {
          const errorMsg = response.data && response.data.msg || '';
          this.$message.error(errorMsg);
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error('');
      } finally {
        this.basicSaving = false;
      }
    },

    // 
    async savePassword() {
      // 
      if (!this.validatePassword()) {
        this.$message.warning('8');
        return;
      }

      if (!this.validatePasswordConfirmation()) {
        this.$message.warning('');
        return;
      }

      if (!this.passwordForm.currentPassword) {
        this.$message.warning('');
        return;
      }

      this.passwordSaving = true;

      try {
        // 
        const passwordData = {
          currentPassword: this.passwordForm.currentPassword,
          newPassword: this.passwordForm.newPassword
        };

        // 
        const response = await putRequest("/updatePassword", passwordData);

        if (response.status === 200 && response.data.status === 'success') {
          this.$message.success('');

          // 
          this.passwordForm = {
            currentPassword: '',
            newPassword: '',
            confirmPassword: ''
          };
        } else {
          const errorMsg = response.data && response.data.msg || '';
          this.$message.error(errorMsg);
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error('');
      } finally {
        this.passwordSaving = false;
      }
    }
  }
}
</script>

<style scoped>
.profile-container {
  display: flex;
  padding: 10px;
}

.card-header {
  border-bottom: 1px solid #ebeef5;
  padding-bottom: 15px;
}

.avatar-section {
  flex: 0 0 150px;
  padding: 15px;
  text-align: center;
}

.info-section {
  flex: 1;
  padding: 10px;
}

.avatar-actions {
  margin-top: 15px;
}

.form-group {
  margin-bottom: 20px;
}

.form-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ebeef5;
  font-weight: bold;
}

.section-title {
  margin-left: 8px;
  color: #1e5799;
  font-size: 16px;
}

.el-form-item {
  margin-bottom: 18px;
}

.el-tag {
  margin-right: 10px;
}

/*  -  */
@media (max-width: 768px) {
  .profile-container {
    flex-direction: column;
  }

  .avatar-section {
    margin-bottom: 20px;
  }
}
</style>



